%test {
  position: absolute;
	content: '';
	transition: all .5s;
}

.boton_nuevo_inicio {
    margin-top: 5px;
    margin-left: 5px;
    padding: 5px 15px 5px 15px;
    border-radius: 0px 25px 25px ;

    background: #e53935;
    background: linear-gradient(to left, #e53935 , #e35d5b); 
	outline: none;
    /* display: inline-block;  */
	padding: 10px 20px;
	color: white;
	/* position: absolute; */
	/* top: 50%; */
	/* left: 50%; */
	transform: translate(5%, 5%);
	vertical-align: middle;
	font-family: 'Rubik One', sans-serif;
	text-decoration: none;
	font-size: 4vw;
	transition: all .5s;
	background-color: #3498db;

	&::before {
    @extend %test;
		bottom: -15px;
		height: 15px;
		width: 100%;
		left: 8px;
		transform: skewX(45deg);
		background-color: darken(#3498db, 20%);
	}

	&::after {
		@extend %test;
		right: -15px;
		height: 100%;
		width: 15px;
		bottom: -8px;
		transform: skewY(45deg);
		background-color: darken(#3498db, 30%);
	}

	&:active {
		margin-left: 10px;
		margin-top: 10px;
		&::before {
			bottom: -5px;
			height: 5px;
			left: 3px;
		}
		
    &::after {
			right: -5px;
			width: 5px;
			bottom: -3px;
		}	
	}
}


